export default {
    layout: "Dum-content",

    mounted() {
        this.getNotice();
        this.getEvent();
        this.getSlider();
        this.getProgram() 
    },
    data(){
        return {
            notices: [], 
            events: [], 
            sliders: [], 
            programs:[],         
            
        }
    },
    methods: {
         getNotice() {
      this.$axios
        .$get("/notice/show")
        .then((res) => {           
          this.notices =res.length;
        })
        .catch((err) => {
          console.log(err);
        });
    },
     getEvent() {
      this.$axios
        .$get("/event/show")
        .then((res) => {
          this.events = res.length;
        })
        .catch((err) => {
          console.log(err);
        });
    },
       getSlider() {
      this.$axios
        .$get("/slider/show")
        .then((res) => {
          this.sliders = res.length;
        })
        .catch((err) => {
          console.log(err);
        });
    },
      getProgram() {
      this.$axios
        .$get("/program/show")
        .then((res) => {
          this.programs = res.length;
        })
        .catch((err) => {
          console.log(err);
        });
    },
    }
    
    

}
